@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto+Condensed&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

:root {
  --main-light-color: #84DCC6;
  --main-medium-color: #4B4E6D;
  --main-print-color: #222222;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.player-card {
  grid-column: 1 / 3;
  width: 100%;
  position: relative;
  padding: 0.5rem .5rem .5rem 1rem;
  color:#ffffff;
  background-color: var(--main-medium-color);
  display: flex;
  align-items: center;
}

.player-card:nth-of-type(1),
.player-card:nth-of-type(2),
.player-card:nth-of-type(3) {
  padding: .5rem;
  background-color: transparent;
  color: var(--main-text-color);
  flex-direction: column;
  font-size: 12px;
}

.player-card:nth-of-type(1) {
  border: 2px solid #FFD700;
  width: 50%;
}

.player-card:nth-of-type(1) .player-pic {
  background-color: #FFD700;
}

.player-card:nth-of-type(2) {
  border: 2px solid #C0C0C0;
  grid-column: 1 / 2;
}

.player-card:nth-of-type(2) .player-pic {
  background-color: #C0C0C0;
}

.player-card:nth-of-type(3) {
  border: 2px solid #CD7F32;
  grid-column: 2 / 3;
}

.player-card:nth-of-type(3) .player-pic {
  background-color: #CD7F32;
}

.player-position {
  position: absolute;
  top: 3px;
  left: 6px;
  color: var(--main-light-color);
  font-size: 1.5rem;
  font-weight: bold;
}

.player-pic {
  border-radius: 50%;
  max-width: 75px;
  text-align: center;
  background-color: #ffffff;
}

.player-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex: 1 1 auto;
}

.player-content > * {
  flex: 1 1 110px;
  display: grid;
  place-items: center;
  margin: .5rem auto;
}

.player-heading {
  color: var(--main-light-color);
  flex: 1 1 275px;
}

@media screen and (min-width: 600px) {
  .player-card {
    padding: .5rem .5rem .5rem 2rem;
  }

  .player-card:nth-of-type(1),
  .player-card:nth-of-type(2),
  .player-card:nth-of-type(3) {
    padding: .5rem;
    font-size: 16px;
  }

  .player-position {
    top: 6px;
    left: 12px;
  }

  .player-content > * {
    flex: 1 1 200px;
  }

  .player-heading {
    flex: 1 1 275px;
  }
}

@media screen and (min-width: 700px) {
  .player-content > * {
    flex: 1 1 150px;
  }
  .player-heading {
    flex: 1 1 250px;
  }
}

@media screen and (min-width: 1000px) {
  .player-card {
    padding: 1rem 1rem 1rem 4rem;
  }

  .player-card:nth-of-type(1),
  .player-card:nth-of-type(2),
  .player-card:nth-of-type(3) {
    padding: 1rem;
  }

  .player-content > * {
    flex: 1 1 200px;
  }

  .player-content-inner {
    display: flex;
  }
  .player-card:nth-of-type(1) .player-content-inner,
  .player-card:nth-of-type(2) .player-content-inner,
  .player-card:nth-of-type(3) .player-content-inner {
    flex-direction: column;
  }

  .player-metric {
    display: inline-block;
    margin: 0 .5rem;
  }
}
.loading-spinner {
  width: 7.5em;
  height: 9em;
  border: 8px solid transparent;
  border-top-color: var(--main-light-color);
  border-bottom-color: var(--main-light-color);
  border-radius: 50%;
  animation: spin-stretch 2s ease infinite;
  margin-top: 6rem;
  grid-column: 1 / 3;
}

@keyframes spin-stretch {
  50% {
    transform: rotate(360deg) scale(0.4, 0.33);
    border-width: 12px;
  }
  100% {
    transform: rotate(720deg) scale(1, 1);
    border-width: 5px;
  }
}

.error-message {
  color: #b63939;
  grid-column: span 2;
  text-align: center;
  width: 60%;
}
.lb-header {
  text-align: center;
  padding-top: 2rem;
  color: var(--main-medium-color);
}

.player-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  width: 90%;
  max-width: 55rem;
  margin: 2rem auto;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.handle-error {
  color: red;
}


